<template>
  <gauge
      title="windspeed"
      height="200"
      width="200"
      unit="m/s"
      :value="9"
      :max="10"
    />
</template>

<script>

import Linechart from './Linechart'
import Gauge from './Gauge'
import Panel from './Panel'

import { text, csvParseRows } from 'd3'

export default {
  name: 'test',
  components: { Linechart, Gauge, Panel },
  data: function(){
    return {
      url: 'https://julianoliver.com/harvest/nodes/node1.csv',
      stats: []
    }
  },
  methods: {
    updateCsv: function(){
      const header = ['timestamp', 'windspeed','outsidetemp','load','gputemp']
      text(this.url, text => {
        const parsed = csvParseRows(text)[0]
        const data = {}
        header.forEach((name, i) => {
          data[name] = parsed[i]
        })
        data.date = new Date(data.timestamp*1000)
        console.log(data)
        this.stats.push(data)
      })
    }
  },
  mounted: function(){
    this.updateCsv()
  }
}
</script>

<style scoped>

</style>



